.markdown{
	border: none
}

.markdown>p, 
.markdown>ul, 
.markdown>ol,
.markdown>h1,
.markdown>h2,
.markdown>h3,
.markdown>h4,
.markdown>h5,
.markdown>h6,
.markdown>blockquote{
	margin-bottom: calc(var(--base-line-height) - 0.75em);
}

.markdown>hr{
	margin: 1.25em 0;
}

.markdown>ul,
.markdown>ol{
	padding-left: 2em;
}

.markdown>blockquote{
	border: 1px solid var(--grey-color);
	border-radius: 3px;
	background-color: var(--note-yellow);
	font-family: Georgia, var(--base-font-family);
	font-size: var(--big-font-size);
	padding: 0.5em 0.75em;
}

.markdown>hr{
	height: 1px;
	background-color: var(--grey-color);
}

.markdown>p>img{
        max-width: 100%;
}
.markdown>*>a {
    color: var(--accent-color);
}

.markdown>h6{
	font-size: var(--small-font-size);
}
.markdown>h5{
	font-size: var(--base-font-size);
}
.markdown>h4{
	font-size: calc(var(--base-font-size) + 1pt);
}
.markdown>h3{
	font-size: calc(var(--base-font-size) + 2pt);
}
.markdown>h2{
	font-size: calc(var(--base-font-size) + 4pt);
}
.markdown>h1{
	font-size: calc(var(--base-font-size) + 8pt);
}



